import React from 'react';

import { Container } from './styles';

const Services = () => {
    return (
        <Container>
            <div className="content">
                <i></i>
                <h3>Consultas</h3>
                <p>Nossa equipe de veterinrios  formada por profissionais de quase todas as especialidades que levam sade e qualidade de vida para voc e seus pets.</p>
            </div>
            <div className="content">
                <i></i>
                <h3>Exames</h3>
                <p> isso mesmo! Voc pode agendar e realizar diversos tipos de exames sem sair da sua casa! Nossa equipe conta com profissionais especializados em diversas reas.</p>
            </div>
            <div className="content">
                <i></i>
                <h3>Vacinas</h3>
                <p>De forma segura e prtica, sem filas nem stress, que pode ser causado em ambientes estranhos, voc pode vacinar o seu pet no conforto do seu sof!</p>
            </div>
        </Container>
    );
}

export default Services;